<template lang="pug">
  section.options-board
    p 
    .hr
    input#sale(type="checkbox" v-model="saleOnGameoverCheckbox")
    label(for="sale")    ,   .
    p(@click="closeOptions") 
    .hr
    p.info        . 
      |  ,   .
      |    ,        , 
      |            .
      |     alpha -     ,      .
      |         
      a(href="mailto:pomatsuev@gmail.com") pomatsuev()gmail.com
</template>

<script>
import { mapState, mapMutations } from 'vuex'
export default {
  name: 'OptionsBoard',
  computed: {
    ...mapState(['saleOnGameOver']),
    saleOnGameoverCheckbox: {
      get() {
        return this.saleOnGameOver;
      },
      set(value){
        this.switchSaleOnGameOver(value);
      }
    }
  },
  methods:{
    ...mapMutations(['switchSaleOnGameOver','closeOptions'])
  }
}
</script>

<style lang="scss" scoped>
  .options-board{
    font-size: 16pt;
    text-align: center;
    .info {
      font-size: 20pt;
    }
    a{
      display: inline;
    }
  }
</style>